/* -------------------------------- 

Primary style

-------------------------------- */
html * {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

*,
*:after,
*:before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

body {
  font-size: 100%;
  font-family: "Droid Serif", serif;
  color: #7f8c97;
  background-color: #e9f0f5; }

a {
  color: #acb7c0;
  text-decoration: none;
  font-family: "Open Sans", sans-serif; }

img {
  max-width: 100%; }

h1,
h2 {
  font-family: "Open Sans", sans-serif;
  font-weight: bold; }

/* -------------------------------- 

Modules - reusable parts of our design

-------------------------------- */
.container {
  /* this class is used to give a max-width to the element it is applied to, and center it horizontally when it reaches that max-width */
  width: 90%;
  max-width: 100%;
  margin: 0 auto; }
  .container::after {
    /* clearfix */
    content: "";
    display: table;
    clear: both; }

/* -------------------------------- 

Main components 

-------------------------------- */
header {
  height: 200px;
  line-height: 200px;
  text-align: center;
  background: #303e49; }
  header h1 {
    color: #ffffff;
    font-size: 18px;
    font-size: 1.125rem; }
  @media only screen and (min-width: 1100px) {
    header {
      height: 300px;
      line-height: 300px; }
      header h1 {
        font-size: 24px;
        font-size: 1.5rem; } }

#timeline {
  position: relative;
  padding: 2em 0;
  margin-top: 2em;
  margin-bottom: 2em; }
  #timeline::before {
    /* this is the vertical line */
    content: "";
    position: absolute;
    top: 0;
    left: 18px;
    height: 100%;
    width: 4px;
    background: #d7e4ed; }
  @media only screen and (min-width: 1100px) {
    #timeline {
      margin-top: 3em;
      margin-bottom: 3em; }
      #timeline::before {
        left: 50%;
        margin-left: -2px; } }

.timeline-block {
  position: relative;
  margin: 2em 0; }
  .timeline-block:before, .timeline-block:after {
    content: "";
    display: table; }
  .timeline-block:after {
    clear: both; }
  .timeline-block:first-child {
    margin-top: 0; }
  .timeline-block:last-child {
    margin-bottom: 0; }
  @media only screen and (min-width: 1100px) {
    .timeline-block {
      margin: 4em 0; }
      .timeline-block:first-child {
        margin-top: 0; }
      .timeline-block:last-child {
        margin-bottom: 0; } }

.timeline-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  -webkit-box-shadow: 0 0 0 4px #ffffff, inset 0 2px 0 rgba(0, 0, 0, 0.08), 0 3px 0 4px rgba(0, 0, 0, 0.05);
          box-shadow: 0 0 0 4px #ffffff, inset 0 2px 0 rgba(0, 0, 0, 0.08), 0 3px 0 4px rgba(0, 0, 0, 0.05); }
  .timeline-img img {
    display: block;
    width: 24px;
    height: 24px;
    position: relative;
    left: 50%;
    top: 50%;
    margin-left: -12px;
    margin-top: -12px; }
  .timeline-img.picture {
    background: #333333; }
  .timeline-img.movie {
    background: #f49b00; }
  .timeline-img.location {
    background: #cccccc; }
  @media only screen and (min-width: 1100px) {
    .timeline-img {
      width: 60px;
      height: 60px;
      left: 50%;
      margin-left: -30px;
      /* Force Hardware Acceleration in WebKit */
      -webkit-transform: translateZ(0);
      -webkit-backface-visibility: hidden; }
      .cssanimations .timeline-img.is-hidden {
        visibility: hidden; }
      .cssanimations .timeline-img.bounce-in {
        visibility: visible;
        -webkit-animation: bounce-1 0.6s;
        animation: bounce-1 0.6s; } }

@-webkit-keyframes bounce-1 {
  0% {
    opacity: 0;
    -ms-transform: scale(0.5);
    -webkit-transform: scale(0.5);
    transform: scale(0.5); }
  60% {
    opacity: 1;
    -ms-transform: scale(1.2);
    -webkit-transform: scale(1.2);
    transform: scale(1.2); }
  100% {
    -ms-transform: scale(1);
    -webkit-transform: scale(1);
    transform: scale(1); } }

@keyframes bounce-1 {
  0% {
    opacity: 0;
    -ms-transform: scale(0.5);
    -webkit-transform: scale(0.5);
    transform: scale(0.5); }
  60% {
    opacity: 1;
    -ms-transform: scale(1.2);
    -webkit-transform: scale(1.2);
    transform: scale(1.2); }
  100% {
    -ms-transform: scale(1);
    -webkit-transform: scale(1);
    transform: scale(1); } }

.timeline-content {
  position: relative;
  margin-left: 60px;
  background: #ffffff;
  border-radius: 0.25em;
  padding: 1em;
  -webkit-box-shadow: 0 3px 0 #d7e4ed;
          box-shadow: 0 3px 0 #d7e4ed; }
  .timeline-content:before, .timeline-content:after {
    content: "";
    display: table; }
  .timeline-content:after {
    clear: both; }
  .timeline-content h2 {
    color: #303e49; }
  .timeline-content p,
  .timeline-content .read-more,
  .timeline-content .date {
    font-size: 13px;
    font-size: 0.8125rem; }
  .timeline-content .read-more,
  .timeline-content .date {
    display: inline-block; }
  .timeline-content p {
    margin: 1em 0;
    line-height: 1.6; }
  .timeline-content .read-more {
    float: right;
    padding: 0.8em 1em;
    background: #acb7c0;
    color: #ffffff;
    border-radius: 0.25em; }
    .no-touch .timeline-content .read-more:hover {
      background-color: #bac4cb; }
  .timeline-content .date {
    float: left;
    padding: 0.8em 0; }
  .timeline-content::before {
    content: "";
    position: absolute;
    top: 16px;
    right: 100%;
    height: 0;
    width: 0;
    border: 7px solid transparent;
    border-right: 7px solid #ffffff; }
  @media only screen and (min-width: 768px) {
    .timeline-content h2 {
      font-size: 20px;
      font-size: 1.25rem; }
    .timeline-content p {
      font-size: 16px;
      font-size: 1rem; }
    .timeline-content .read-more,
    .timeline-content .date {
      font-size: 14px;
      font-size: 0.875rem; } }
  @media only screen and (min-width: 1100px) {
    .timeline-content {
      margin-left: 0;
      padding: 1.6em;
      width: 45%; }
      .timeline-content::before {
        top: 24px;
        left: 100%;
        border-color: transparent;
        border-left-color: #ffffff; }
      .timeline-content .read-more {
        float: left; }
      .timeline-content .date {
        position: absolute;
        width: 100%;
        left: 82%;
        top: 6px;
        font-size: 16px;
        font-size: 1rem; }
      .timeline-block:nth-child(even) .timeline-content {
        float: right; }
        .timeline-block:nth-child(even) .timeline-content::before {
          top: 24px;
          left: auto;
          right: 100%;
          border-color: transparent;
          border-right-color: #ffffff; }
        .timeline-block:nth-child(even) .timeline-content .read-more {
          float: right; }
        .timeline-block:nth-child(even) .timeline-content .date {
          left: auto;
          right: 122%;
          text-align: right; }
      .cssanimations .timeline-content.is-hidden {
        visibility: hidden; }
      .cssanimations .timeline-content.bounce-in {
        visibility: visible;
        -webkit-animation: bounce-2 0.6s;
        animation: bounce-2 0.6s; } }

@media only screen and (min-width: 1100px) {
  /* inverse bounce effect on even content blocks */
  .cssanimations
.timeline-block:nth-child(even)
.timeline-content.bounce-in {
    -webkit-animation: bounce-2-inverse 0.6s;
    animation: bounce-2-inverse 0.6s; } }

@-webkit-keyframes bounce-2 {
  0% {
    opacity: 0;
    -ms-transform: translateX(-100px);
    -webkit-transform: translateX(-100px);
    transform: translateX(-100px); }
  60% {
    opacity: 1;
    -ms-transform: translateX(20px);
    -webkit-transform: translateX(20px);
    transform: translateX(20px); }
  100% {
    -ms-transform: translateX(0);
    -webkit-transform: translateX(0);
    transform: translateX(0); } }

@keyframes bounce-2 {
  0% {
    opacity: 0;
    -ms-transform: translateX(-100px);
    -webkit-transform: translateX(-100px);
    transform: translateX(-100px); }
  60% {
    opacity: 1;
    -ms-transform: translateX(20px);
    -webkit-transform: translateX(20px);
    transform: translateX(20px); }
  100% {
    -ms-transform: translateX(0);
    -webkit-transform: translateX(0);
    transform: translateX(0); } }

@-webkit-keyframes bounce-2-inverse {
  0% {
    opacity: 0;
    -ms-transform: translateX(100px);
    -webkit-transform: translateX(100px);
    transform: translateX(100px); }
  60% {
    opacity: 1;
    -ms-transform: translateX(-20px);
    -webkit-transform: translateX(-20px);
    transform: translateX(-20px); }
  100% {
    -ms-transform: translateX(0);
    -webkit-transform: translateX(0);
    transform: translateX(0); } }

@keyframes bounce-2-inverse {
  0% {
    opacity: 0;
    -ms-transform: translateX(100px);
    -webkit-transform: translateX(100px);
    transform: translateX(100px); }
  60% {
    opacity: 1;
    -ms-transform: translateX(-20px);
    -webkit-transform: translateX(-20px);
    transform: translateX(-20px); }
  100% {
    -ms-transform: translateX(0);
    -webkit-transform: translateX(0);
    transform: translateX(0); } }



